import locales from '../../i18n';

export const getUserLanguage = () => {
  const { siteLanguage: defaultLanguage } = locales.find(locale => locale.default);
  if (typeof navigator === `undefined`) {
    return defaultLanguage;
  }

  const lang = navigator && navigator.language && navigator.language.split('-')[0];
  if (locales.some(locale => locale.siteLanguage === lang)) return lang;
  else return defaultLanguage;
};

export const getLocalizedPath = (locale: string, path: string) => {
  const userLocale = locales.find(searchedLocale => searchedLocale.id == locale);
  return userLocale.default ? path : `/${locale}${path}`;
};
