import React from 'react';
import { Link } from 'gatsby';

import Layout from '../components/layout';
import { getLocalizedPath } from '../utils';

import content from '../../site/content/newsletter-confirmation.json';

interface Props {
  pageContext: {
    locale: string;
  };
}

const NewsletterSuccess = ({ pageContext }: Props) => {
  const { locale } = pageContext;
  return (
    <Layout locale={locale}>
      <>
        <div className="container min-h-screen flex flex-col items-center justify-center">
          <h1 className="text-5xl sm:text-6xl md:text-8xl text-white text-center">
            {content[locale].title}
          </h1>
          <h3 className="text-xl sm:text-3xl text-grey text-center">
            {content[locale].description}
          </h3>
          <Link
            to={getLocalizedPath(locale, '/')}
            className="bg-blue text-white p-2 mt-8 rounded max-w-max flex items-center mx-auto transition duration-500 ease-in-out transform hover:-translate-y-1 hover:scale-110 md:-mb-32"
          >
            <p>{content[locale].button}</p>
          </Link>
        </div>
      </>
    </Layout>
  );
};

export default NewsletterSuccess;
