import React, { useEffect, useState } from 'react';
import { Link } from 'gatsby';
import { FaAlignRight, FaTimes } from 'react-icons/fa';

import exordeLogo from '../images/pyramid.svg';
import Sidebar from './sidebar';

import content from '../../site/content/components/navbar.json';
interface Props {
  locale: string;
}

const Navbar = ({ locale }: Props) => {
  const [openSidebar, setOpenSidebar] = useState(false);
  const [scrollY, setScrollY] = useState(0);

  const onScroll = () => setScrollY(window.pageYOffset);

  useEffect(() => {
    const watchScroll = () => window.addEventListener('scroll', onScroll);
    watchScroll();
    return () => {
      window.removeEventListener('scroll', onScroll);
    };
  });
  return (
    <>
      <nav
        className={`fixed top-0 py-2 h-20 w-full flex items-center z-50 text-white ${
          scrollY > 0 ? 'shadow bg-gradient-to-l from-black to-blue-dark' : 'bg-transparent'
        }`}
      >
        <div className="container flex items-center justify-between">
          <Link to="/" className="h-28 flex items-center">
            <img src={exordeLogo} alt="exorde logo" className="h-12" />
            <h3 className={`font-display ml-2 text-2xl mt-4`}>EXORDE</h3>
          </Link>
          <ul className="hidden md:flex">
            {content[locale].items.map(item => (
              <Link to={item.link}>
                <li className="p-2">{item.label}</li>
              </Link>
            ))}
          </ul>
          {content[locale].items.length > 0 && (
            <button
              type="button"
              onClick={() => setOpenSidebar(!openSidebar)}
              className="ml-auto text-3xl text-white md:hidden"
            >
              {openSidebar ? <FaTimes /> : <FaAlignRight />}
            </button>
          )}
        </div>
      </nav>
      <Sidebar open={openSidebar} items={content[locale].items} />
    </>
  );
};

export default Navbar;
