import { Link } from 'gatsby';
import React from 'react';

interface Props {
  open: boolean;
  items: {
    label: string;
    link: string;
  }[];
}

const Sidebar = ({ open, items }: Props) => (
  <div
    id="sidebar"
    className={`${
      open ? 'visible sticky' : 'hidden'
    }  h-screen w-full top-0 pt-20 z-40 bg-gradient-to-b from-blue-dark to-black`}
  >
    <ul className="text-center  text-white font-semibold text-4xl pt-20">
      {items.map(item => (
        <Link to={item.link}>
          <li className="p-4">{item.label}</li>
        </Link>
      ))}
    </ul>
  </div>
);

export default Sidebar;
